.navbar {
    background-color: rgb(1, 7, 19);
    height: 66px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    /* display: flex; */
    display: block ruby;
    /* justify-content: center; */
    /* align-items: center; */
    /* height: 80px; */
    max-width: 1500px;
  }

  .navbar-container i{}
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-top: 10px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: flex;
    /* grid-template-columns: repeat(4, auto); */
    /* grid-gap: 10px; */
    list-style: none;
    text-align: center;
    height: 50px;
    width: 84vw;
    justify-content: end;
    /* margin-right: 2rem; */
    flex-direction: row;
    /* flex-wrap: wrap; */
    align-items: center;
  }
  
  .nav-item {
    height: 85px;
    margin-top: 70px;
  }
  
  .nav-links {
    color: #fff;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    /* display: flex; */
    position: relative;
  }
  
  .nav-links::after {
    content: '';
    width: 0;
    height: 3px;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-46%);
    background:#fff;
    transition: width 0.3s;  
  }
  
  .nav-links:hover::after{
    width: 50%;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 0.9;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      /* padding: 2rem; */
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  }

